<template>
  <span v-if="isFunction" class="italic">
    <template v-if="deepth === 0">
      <span v-if="isArrowFunction">{{value}}</span>
      <span v-else>
        <span class="function"></span><span> {{value.name}}()</span>
      </span>
    </template>
    <template v-else>
      <span class="function"></span>
    </template>
  </span>
  <span v-else-if="isObject">
    <span>{</span>
    <template v-if="deepth === 0">
      <span v-for="(name, index) in displayPropertyNames" :key="index" v-if="index < maxDisplayPropertyCount">
        <span>{{name}}: </span>
        <text-inline-block :value="value[name]" :deepth="deepth + 1" />
        <span v-if="index !== Math.min(maxDisplayPropertyCount - 1, displayPropertyNames.length - 1)">, </span>
      </span>
      <span v-if="displayPropertyNames.length >= 5">, ...</span>
    </template>
    <!--  1  -->
    <template v-else>
      <span>...</span>
    </template>
    <span>}</span>
  </span>
  <span v-else :class="valueClass">{{formattedValue}}</span>
</template>

<script>
/**
 * 
 * 
 *  obj
 * obj = {
 *  a: 1,
 *  b: 2
 * }
 * UI 
 * {a: 1, b: 2}
 * 
 *  chrome devtools 
 * 1 5 
 * obj = {
 *  a: 1,
 *  b: 2,
 *  c: 3,
 *  d: 4,
 *  e: 5,
 *  f: 6
 * }
 * UI 
 * {a: 1, b: 2, c: 3, d: 4, e: 5, ...}
 * 
 * 2
 * obj = {
 *  // 
 *  a: 1,
 *  // 
 *  b: 1,
 *  // get 
 *  c: (...)
 * }
 * UI 
 * {a: 1, b: 1}
 * 
 * 3 0   0 
 * obj = {
 *  a: () => {},
 *  b: function () {},
 *  c: function say () {}
 * }
 *  0 
 * {a: , b: , c: }
 *  0 
 * {
 *  a: () => {}
 *  b:  (),
 *  c:  say()
 * }
 */
import {
  isNull,
  isBoolean,
  isString,
  isNumber,
  isObject,
  isSymbol,
  isUndefined,
  flatMap,
  _console,
  isFunction
} from '@/utils'

export default {
  name: 'text-inline-block',
  props: {
    // 
    value: {},
    // 
    // let obj = {a: {b: 2}}
    // obj  deepth  0
    // obj.a  deepth  1
    // obj.a.b  deepth  2 
    deepth: {
      type: Number,
      default: 0
    }
  },
  computed: {
    isFunction () {
      return isFunction(this.value)
    },
    isArrowFunction () {
      return isFunction(this.value) && String(this.value).indexOf('() =>') === 0
    },
    isObject () {
      return isObject(this.value)
    },
    maxDisplayPropertyCount () {
      return 5
    },
    displayPropertyNames () {
      const obj = this.value
      return Object.getOwnPropertyNames(obj)
        // 
        .filter(name => {
          const descriptor = Object.getOwnPropertyDescriptor(obj, name)
          return 'value' in descriptor
        })
    },
    formattedValue () {
      const value = this.value
      if (isNull(value) || isUndefined(value)) {
        return String(value)
      }
      // 
      if (isString(value) && this.deepth > 0) {
        return JSON.stringify(value)
      }
      return value
    },
    valueClass () {
      const value = this.value
      return {
        null: isNull(value),
        undefined: isUndefined(value),
        boolean: isBoolean(value),
        number: isNumber(value),
        // key
        string: isString(value) && this.deepth > 0,
        symbol: isSymbol(value)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.normal {
  color: #565656;
}
.null, .undefined {
  color: rgb(128, 128, 128);
}
.boolean, .function {
  color: rgb(13, 34, 170);
}
.number {
  color: #1C00CF;
}
.string, .symbol {
  color: rgb(196, 26, 22);
}
.string-quote {
  color: #222;
}
.italic {
  font-style: italic;
}
</style>



