<template>
  <div class="text-block">
    <!--  -->
    <div class="prop" @click.stop="isFold = !isFold">
      <!-- (00) -->
      <div v-if="indentSize > 0 || properties.length > 0" class="indent" :style="indentStyle">
        <div class="triangle" :class="arrowClass"></div>
      </div>
      <!--  -->
      <template v-if="hasName">
        <span class="key" :class="[descriptor.enumerable ? 'public' : 'private']">{{name}}</span>
        <span class="space">: </span>
      </template>
      <!--  -->
      <!-- get  -->
      <span v-if="isGetAccessor" @click="onClickGetAccessor">{{computedValue}}</span>
      <!-- <span v-else :class="valueClass">{{formattedValue}}</span>   -->
      <text-inline-block
        v-else
        :value="descriptor.value"
      />
    </div>
    <!--  -->
    <!--  v-show  -->
    <div v-for="(item, index) in properties" :key="String(item.name) + index" v-show="!isFold">
      <text-block
        :name="item.name"
        :descriptor="item.descriptor"
        :needComputeProps="!isFold"
        :indentSize="indentSize + 1"
      />
    </div>
  </div>
</template>

<script>
/**
 * 
 * 
 *  obj
 * obj = {
 *  a: 1,
 *  b: 2
 * }
 * UI 
 * {
 *  a: 1,
 *  b: 2
 * }
 * 
 *  chrome devtools 
 * 1...
 * 
 * 
 * // 
 * <text-block :descriptor="{value: 1}" />
 * // 
 * <text-block :name="age" :descriptor="{value: 1}" />
 */
import {
  isNull,
  isBoolean,
  isString,
  isNumber,
  isObject,
  isSymbol,
  isUndefined,
  flatMap,
  _console,
isFunction
} from '@/utils'
import TextInlineBlock from './TextInlineBlock'

export default {
  name: 'text-block',
  components: {
    TextInlineBlock
  },
  props: {
    // 
    name: [String, Symbol],
    //  Object.defineProperty() 
    descriptor: {
      type: Object,
      required: true,
      validator: descriptor => {
        try {
          Object.defineProperty({}, 'key', descriptor)
        } catch (error) {
          _console.error(error.message, 'descriptor:', descriptor)
        }
        // ConsolePanel 
        return true
      }
    },
    // 
    needComputeProps: {
      type: Boolean,
      default: true
    },
    // 
    indentSize: {
      type: Number,
      default: 1
    }
  },
  data () {
    return {
      // 
      isFold: true,
      //  get  descriptor  get 
      hasComputed: false,
      //  get  descriptor  get 
      computedValue: '(...)'
    }
  },
  computed: {
    hasName () {
      return !!this.name
    },
    isGetAccessor () {
      return typeof this.descriptor.get === 'function'
    },
    // 
    properties () {
      // 
      if (!this.needComputeProps) {
        return []
      }

      // 
      //  value  getter 
      let value
      if (this.isGetAccessor) {
        if (this.hasComputed) {
          value = this.computedValue
        } else {
          return []
        }
      } else {
        value = this.descriptor.value
      }

      let obj
      if (isObject(value)) {
        obj = value
      } else if (isFunction(value)) {
        obj = value
      } else {
        return []
      }

      // 
      const ownKeys = [...Object.getOwnPropertyNames(obj), ...Object.getOwnPropertySymbols(obj)]
      const properties = flatMap(ownKeys, (name) => {
        const descriptor = Object.getOwnPropertyDescriptor(obj, name)
        if (!descriptor) {
          return []
        }

        // value and writable  get/set 
        if (isFunction(descriptor.get) && isFunction(descriptor.set)) { //  getter  setter
          return [
            {name, descriptor},
            {
              name: `get ${name}`,
              descriptor: {
                value: descriptor.get,
                enumerable: false
              }
            },
            {
              name: `set ${name}`,
              descriptor: {
                value: descriptor.set,
                enumerable: false
              }
            }
          ]
        } else if (isFunction(descriptor.get) && !isFunction(descriptor.set)) { //  getter 
          return [
            {name, descriptor},
            {
              name: `get ${name}`,
              descriptor: {
                value: descriptor.get,
                enumerable: false
              }
            }
          ]
        } else if (!isFunction(descriptor.get) && isFunction(descriptor.set)) { //  setter
          return [
            {
              name: `set ${name}`,
              descriptor: {
                value: descriptor.set,
                enumerable: false
              }
            }
          ]
        } else { //  getter  setter value 
          return [{name, descriptor }]
        }

        // _console.log(name, ':', descriptor)
      })
        // TODO: JSON.stringify  JSON 
        //  https://stackoverflow.com/questions/4816099/chrome-sendrequest-error-typeerror-converting-circular-structure-to-json#
        // .filter(name => name !== '__ob__')
      
      // '__proto__'
      if (properties.findIndex(v => v.name === '__proto__') === -1) {
        properties.push({
          name: '__proto__',
          descriptor: {
            value: obj.__proto__,
            enumerable: false,
            configurable: true
          }
        })
      }

      // Object.prototype '__proto__' get/set 
      if (obj === Object.prototype) {
        let index = properties.findIndex(v => v.name === '__proto__')
        if (index !== -1) {
          properties.splice(index, 1)
          const descriptor = Object.getOwnPropertyDescriptor(obj, '__proto__')
          properties.push({
            name: 'get __proto__',
            descriptor: {
              value: descriptor.get,
              enumerable: false
            }
          }, {
            name: 'set __proto__',
            descriptor: {
              value: descriptor.set,
              enumerable: false
            }
          })
        }
      }

      // 
      return properties.sort(propCompareFn)
    },
    indentStyle () {
      return {
        width: this.indentSize > 0 ? `${this.indentSize}em` : 'auto' 
      }
    },
    // /CSS
    arrowClass () {
      return this.properties.length > 0 ? (this.isFold ? 'fold' : 'unfold') : ''
    },
  },
  methods: {
    onClickGetAccessor () {
      if (!this.hasComputed) {  // 
        this.hasComputed = true
        try {
          //  Vue 
          this.computedValue = Object.freeze(this.descriptor.get())
        } catch (error) {
          _console.error(error)
          this.computedValue = '(error: ' + error.message + ')'
        }
      }
    }
  }
}

/**
 * 
 * 
 * A public         // 0
 * B public         // 
 * a public         // 
 * b public         // 
 * Symbol() public  // 10
 * Symbol(a) public // 
 * A private        // 20
 * B private        // 
 * a private        // 
 * b private        // 
 * Symbol() private // 30
 * get A            // 40
 * set A            // 
 * get B
 * get a            // 
 * set a            // 
 * get b
 * __proto__        // 100
 */
function getPropDisplayPriority (prop) {
  let priority = 0
  if (isString(prop.name)) {
    if (prop.descriptor.enumerable) {
      priority = 0
    } else {
      if (prop.name.indexOf('get ') === 0 || prop.name.indexOf('set ') === 0) {
        priority = 40
      } else {
        priority = 20
      }
    }
  } else {  // symbol
    if (prop.descriptor.enumerable) {
      priority = 10
    } else {
      priority = 30
    }
  }
  if (prop.name === '__proto__') {
    priority = 100
  }
  return priority
}

/**
 * 
 */
function propCompareFn (propA, propB) {
  let priorityA = getPropDisplayPriority(propA)
  let priorityB = getPropDisplayPriority(propB)
  // _console.log(propA.name, propB.name, priorityA, priorityB)
  if (priorityA === priorityB) { //  ASCII 
    const a = String(propA.name)
    const b = String(propB.name)
    return a < b ? -1 : (a > b ? 1 : 0)
  } else { // 
    return priorityA - priorityB
  }
}
</script>

<style scoped lang="scss">
.public, .private {
  color: rgb(136, 19, 145);
}
.private {
  opacity: 0.6;
}

.text-block {
  display: flex;
  flex-direction: column;
  line-height: 1.4em;
  .indent {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
    padding-right: 3px;
  }
  .triangle {
    width: 0;
    height: 0;
    $border-width: 5px;
    /* tan(30)  0.5773502691896257 */
    &.fold {
      border-left: $border-width solid #727272;
      border-top: $border-width * 0.8 solid transparent;
      border-bottom: $border-width * 0.8 solid transparent;
    }
    &.unfold {
      border-top: $border-width solid #727272;
      border-left: $border-width * 0.8 solid transparent;
      border-right: $border-width * 0.8 solid transparent;
    }
  }
  .content {
    display: flex;
    flex-direction: column;
  }
  .prop {
    display: flex;
    flex-direction: row;
    .key {
      flex-shrink: 0;
    }
  }
  .space {
    white-space: pre;
  }
}
</style>



