<template>
  <div class="message source-code" :class="[type]">
    <div v-if="isErrorCaptured" class="error">
      Message 
    </div>
    <template v-else v-for="(value, index) in values">
      <text-block
        :descriptor="{value}"
        :key="index + '1'"
        :indentSize="0"
      />
      <span v-if="index !== values.length - 1" class="space" :key="index + '2'">{{space}}</span>
    </template>
  </div>
</template>

<script>
import {isString, isNumber, _console} from '@/utils'
import TextBlock from './TextBlock'

export default {
  components: {
    TextBlock
  },
  props: {
    msgId: {
      type: String,
      required: true
    },
    type: {
      type: String,
      required: true
    },
    logArgs: {
      type: Array,
      default () {
        return []
      }
    }
  },
  data () {
    return {
      isErrorCaptured: false
    }
  },
  computed: {
    space () {
      return ' '
    },
    values () {
      let formattedArgs = format(this.logArgs)
      return formattedArgs
    }
  },
  //  console API 
  errorCaptured (error) {
    // 
    _console.error(error)
    // UI 
    this.isErrorCaptured = true
    return false
  },
}

/**
 *  console.log 
 * 
 * console.log('a%sc%se', 'b', 'd') // abcde
 * 'a%sc%se'
 * 1) 'a', '%s', 'c%se': 'b', 'd'
 * 2) 'a', 'b', 'c%se''d'
 * 3) 'abc%se'
 * 4) 
 * @param {Array} logArgs 
 * @param {Number} startPos 
 */
function format (logArgs, startPos = 0) {
  if (!Array.isArray(logArgs)) {
    return logArgs
  }

  if (logArgs.length === 0) {
    return []
  }

  const [arg0, ...restArgs] = logArgs
  // 
  if (!isString(arg0) || restArgs.length === 0) {
    return logArgs
  }

  const matcher = /(%%|%s|%o|%O|%i|%d|%f)/
  // 
  const result = matcher.exec(arg0.substring(startPos))
  if (!result) { // 
    return logArgs
  }
  const placeholder = result[0]
  const placeholderIndex = startPos + result.index // 
  const formattedLogArgs = []
  let part1 = placeholderIndex > 0 ? arg0.substring(0, placeholderIndex) : ''
  let part2 = placeholder
  let part3 = arg0.substring(placeholderIndex + 2)
  if (restArgs.length > 0) {
    if (placeholder === '%%') {
      part2 = '%'
    } else {
      const value = restArgs.shift()
      if (placeholder === '%s') {
        part2 = String(value)
      } else if (placeholder === '%i' || placeholder === '%d') {
        if (isNumber(value)) {
          part2 = parseInt(value, 10)
        } else {
          part2 = NaN
        }
      } else if (placeholder === '%f') {
        if (isNumber(value)) {
          part2 = parseFloat(value, 10)
        } else {
          part2 = NaN
        }
      } else {
        part2 = value
      }
    }
  }

  const nextPos = (part1 + part2).length
  formattedLogArgs.push(...format([part1 + part2 + part3, ...restArgs], nextPos))
  return formattedLogArgs
}

</script>

<style scoped lang="scss">
.message {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 8px 4px;
  overflow-y: scroll;
  &.log, &.debug, &.info {
    border-bottom: 1px solid rgb(240, 240, 240);
  }
  &.error {
    color: red;
    border-top: 1px solid hsl(0, 100%, 92%);
    border-bottom: 1px solid hsl(0, 100%, 92%);
    margin-top: -1px;
    background-color: hsl(0, 100%, 97%);
  }
  &.warn {
    color: hsl(39, 100%, 18%);
    border-top: 1px solid hsl(50, 100%, 88%);
    border-bottom: 1px solid hsl(50, 100%, 88%);
    margin-top: -1px;
    background-color: hsl(50, 100%, 95%);
  }
}

.space {
  white-space: pre;
}
</style>



